/* src/components/ProjectList.module.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  font-family: "Poppins", sans-serif;
  color: white;
}

.project {
  background-color: transparent;
  border: 1px solid #ced4da;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 4px; /* Increase border radius */
  transition: box-shadow 0.3s ease, background-color 0.3s ease; /* Add transition effect for box-shadow and background-color */
}

.project:hover {
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1); /* Increase shadow on hover */
  background-color: transparent;
}

.project h3 {
  margin-bottom: 5px;
}

.link {
  color: #007bff;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.projectList {
  margin: 20px auto;
  text-align: center;
  width: 800px;
}

.projectList ul {
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  background-color: transparent; /* Set background color of the ul to transparent */
  width: 600px;
  height: 100px;
}

.projectList li {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
  width: 600px;
  height: 100px;
  background-color: transparent; /* Set background color of the li to transparent */
}

.projectList li:hover {
  transform: scale(1.2); /* Scale up the list item on hover */
}
