.profile-card-container {
    position: fixed;
    top: 20px;
    right: 20px;
  }
  
  .profile-info {
    text-align: right;
  }
  
  
  .profile-img-container {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    border: 4px solid white;
    margin-bottom: 10px;
  }

  
  .profile-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .profile-name {
    color: white;
    margin: 0;
    font-size: 18px;
  }
  