/* src/components/Navbar.module.css */

.navbar {
    background-color: transparent ;
    margin-left: 120px;
    color: #fff;
    padding-top: 15px;
    transition: padding 0.4s ease; /* Add transition effect for padding */
    /* text-align: center;  */
}

.navbar:hover {
    padding-top: 20px;
    padding-bottom: 20px ; 
}

.navLinks {
    
    list-style: none;
    padding: 10px 0px;
    margin: 0;
    display: inline-block; 
}

.navLinks li {
    display: inline; 
    margin-right: 20px;
    
}

.navLink {
    border-radius: 3px;
    font-size: 15px;
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease, background-color 0.3s ease, height 0.3s ease, width 0.3s ease; /* Add transitions */
    padding: 10px 10px; /* Add padding to create clickable area */
    border: 1px solid white; /* Add border */
    height: 30px; /* Set height */
    width: 150px; /* Set width */

}

.navLink:hover {
    text-decoration: none;
    background-color: #555; /* Change background color on hover */
    width: 80px;
}

.navLink:active {
    color: #ff0; /* Change color when link is clicked */
}

.navLink:hover,
.navLink:active {
    height: 60px; /* Increase height on hover or click */
    width: 100px; /* Increase width on hover or click */
}

