/* Contact.module.css */
.contactContainer {
    display: flex;
    justify-content: center;
}

.contactContent {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: transparent;
    color: white;
}

.inputField {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid white;
    background-color: transparent;
    color: white;
}

/* Added styles for form */
form {
    display: flex;
    flex-direction: column; /* Arrange items vertically */
}

.sendButton {
    width: 50%;
    padding: 10px;
    border: 1px solid white;
    background-color: transparent;
    color: white;
    cursor: pointer;
    align-self: center; /* Align button within its container */
}

.sendButton:hover {
    background-color: white;
    color: black;
}
